<template>
  <div id="school__list">
    <common-header title="申请创业金"></common-header>
    <ul class="user" v-if="applyInfos.length > 0" v-for="(item,index) in applyInfos">
      <li class="user__info" @click="linkToUrl({name:'SchoolDetail',query:{action:1,id:item.id}})">
        <div class="user__info__left">
          <div class="left__img">
            <img :src="item.imagePath" alt="">
          </div>
          <div class="left__name">{{item.name}}</div>
        </div>
        <div class="user__info__right">
          <i class="icon"></i>
          <span>{{item.praiseStat}}</span>
        </div>
      </li>
    </ul>
    <div class="no-apply" v-if="applyInfos.length < 1">
    	暂无申请信息
    </div>
  </div>
</template>
<script type="text/javascript">
	export default{
		data(){
			return{
				applyInfos:[],
			}
		},
		created(){
			this.getApplyList()
		},
		methods:{
			getApplyList(){
				let action = 1
				let id = this.$route.query.id
				this.$Api.getApplyList(action,id).then((res)=>{
				 	console.log(res)
				 	if(res.q.s == 0){
				 		this.applyInfos = res.q.applyInfos
				 	}
				})
			}
		},
		
		
	}
</script>

<style lang="scss">
  #school__list {
    min-height: 100%;
    background: #f0f0f0;

    .user {
      border-bottom: 1px solid #eee;
      
      .user__info {
        display: flex;
        justify-content: space-between;
        padding: 0 .4rem;
        height: 2rem;
        background: #fff;

        .user__info__left {
          display: flex;
          align-items: center;
          
          .left__img {
            margin-right: .4rem;
            width: 1.33rem;
            height: 1.33rem;

            img {
              width: 100%;
              height: 100%;
              border-radius: 50%;
            }
          }

          .left__name {
            font-size: .4rem;
            color: #333;
          }
        }

        .user__info__right {
          display: flex;
          align-items: center;
          font-size: 0.37rem;
          color: #666;
          
          .icon {
            display: inline-block;
            margin-right: .2rem;
            width: 0.37rem;
            height: 0.37rem;
            background: url('../../../assets/school/like_un.png') no-repeat center/100%;

            &.active {
              background: url('../../../assets/school/like.png') no-repeat center/100%;
            }
          }
        }
      }
    }
    .no-apply{
    	line-height: 3rem;
    	text-align: center;
    	color: #999;
    }
  }
</style>



